import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuard } from './common/auth.guard';
import { AdminLayoutComponent } from './layout/admin-layout/admin-layout.component';
import { LoginLayoutComponent } from './layout/login-layout/login-layout.component';
import { LoginComponent } from './login/login.component';
import { RoleComponent } from './user-management/role/role.component';
import { UserComponent } from './user-management/user/user.component';
import { UIRoleComponent } from './user-management/ui-role/ui-role.component';
//import { LayoutComponent } from './layout/layout.component';
//import { MiscReceiptComponent } from './misc-receipt/misc-receipt.component';
import { WizardComponent } from './wizard/wizard.component';

const routes: Routes = [
  { path: '', redirectTo: 'ltc/login', pathMatch: 'full'},

  {
    path: 'ltc',
    component: LoginLayoutComponent,
    children: [
      { path: '', redirectTo: 'login', pathMatch: 'full' },
      { path: 'login', component: LoginComponent }
    ]
  },
  {
    path: 'ltc',
    component: AdminLayoutComponent,
    children: [
      { path: 'asset', component: WizardComponent } ,
      { path: 'user', component: UserComponent } ,
      { path: 'role', component: RoleComponent } ,
      { path: 'uiroleconfig', component: UIRoleComponent } ,
      //{ path: '', loadChildren: () => import('./user-management/user.module').then(m => m.UserModule) },
    ],
    canActivate: [AuthGuard]
  },

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
