import { AfterViewInit, Component, ElementRef, OnInit, ViewChild, HostListener } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { RestService } from '../../services/rest.service';
import { Global } from '../../common/global';
import { Validators, FormBuilder, FormGroup, AbstractControl, FormArray } from '@angular/forms';
import { MatPaginatorModule} from '@angular/material/paginator';
import {  MatSortModule } from '@angular/material/sort';
import {  MatTableModule } from '@angular/material/table';
import {MatAutocompleteModule} from '@angular/material/autocomplete';
import {MatTableDataSource} from "@angular/material/table";
//import {  MatTableDataSource } from '@angular/material';
import { KEY_CODE } from '../../common/constant'; 
import { BehaviorSubject, Observable } from 'rxjs';
import { RequireMatch } from '../../validators/requiredmatch.validator';
import { startWith , map} from 'rxjs/operators';

export interface GridRowObject {
    UIId: number;
    UI: string;
    ParentId: string;
    Viewer: boolean;
    Maker: boolean;
    Checker: boolean;
    Edit: boolean;
    Export: boolean;
    Upload: boolean
}

@Component({
    selector: 'app-ui-role',
    templateUrl: './ui-role.component.html',
    styleUrls: ['./ui-role.component.scss']
})
export class UIRoleComponent implements OnInit {

    // Type = [
    //     { id: 1, name: 'SRN' },
    //     { id: 2, name: 'CAM' }
    //   ];
      selectedType: any = [];

    @ViewChild('form', {static: true}) form:any;
    uiRoleMapStatus: string = 'create';
    menuItems!: any[];
    roleList!: any[];
    displayedColumns = ['RoleId', 'Viewer', 'Maker', 'Checker', 'Edit', 'Export', 'Upload'];
    UIRoleMapData!: GridRowObject [];
    UIRoleMapDataSource = new MatTableDataSource(this.UIRoleMapData);
    dataSource = new BehaviorSubject<AbstractControl[]>([]);
    uimenus : any = [];
    roles: any = [];
    filteredUIMenu!: Observable<any[]>;
    filteredRole!: Observable<any[]>;
    isLoadingResults = false;
    rows: FormArray = this.formBuilder.array([]);
    UIRoleConfigForm: FormGroup = this.formBuilder.group({
        RoleId: [''],
        Role: ['', [Validators.required, RequireMatch]],
        'UIRoleMap': this.rows
    });
    userLoggedIn : any;

    constructor(private formBuilder: FormBuilder, private rest: RestService, private route: ActivatedRoute, private router: Router,
        private global: Global) {      
    }

    ngOnInit() {
        this.userLoggedIn = JSON.parse(localStorage.getItem("userLoggedIn")!);
        this.getMenuList();
        this.getRoleList();
        this.UIRoleMapData = [];
        this.UIRoleMapData.forEach((d: GridRowObject) => this.addRow (d, false));
        this.updateView();
        if(this.Role){
            this.filteredRole = this.Role.valueChanges
            .pipe(
                startWith(null),
                map((value: any | null) => {
                    return value ? this._filterRoles(value) : this.roles.slice()
                })
            );
        }
        
    } 

    emptyTable(){
        while (this.rows.length !== 0){
            this.rows.removeAt(0);
        }
    }

    addRow(d?: GridRowObject, noUpdate?: boolean){
        var uiTitle = this.uimenus.find ((x:any) => x.Id == d?.UIId);
        uiTitle = uiTitle ? uiTitle.Title : '';
        const row = this.formBuilder.group({
            'UIId': [d && d.UIId ? d.UIId: null, []],
            'UI': [d && uiTitle ? uiTitle: null, []],
            'ParentId': [d && d.ParentId ? d.ParentId: null, []],
            'Viewer': [d && d.Viewer ? d.Viewer: null, []],
            'Maker': [d && d.Maker ? d.Maker: null, []],
            'Checker': [d && d.Checker ? d.Checker: null, []],
            'Edit': [d && d.Edit ? d.Edit: null, []],
            'Export': [d && d.Export ? d.Export: null, []],
            'Upload': [d && d.Upload ? d.Upload: null, []],
        });
        this.rows.push(row);
        if(!noUpdate) { this.updateView();}
    }
    updateView(){
        this.dataSource.next(this.rows.controls);
    }

    clearView(){
        this.UIRoleMapData = [];
        this.dataSource.next(this.rows.controls);
    }

    get RoleId () {return this.UIRoleConfigForm.get('RoleId'); }
    get Role () {return this.UIRoleConfigForm.get('Role'); }

    inputRole(role: any){
        this.RoleId?.setValue(null);
    }

    onFocusRole(role: any){
        if(this.Role){
            this.filteredRole = this.Role.valueChanges
            .pipe(
                startWith(null),
                map((value:any | null) => {
                    return value ? this._filterRoles(value) : this.roles.slice()
                })
            );
        }
        
    }

    selectedRole(event:any): void {
        //console.log(event);
        if(event != undefined){
            this.RoleId?.setValue(event.Id);
            this.loadUIRoleMapping(event.Id);
        }
        else{
            this.emptyTable();
            this.updateView();
            this.UIRoleConfigForm.reset();
            this.form.resetForm();
            this.UIRoleConfigForm.markAsUntouched();
        }
        
        // this.RoleId?.setValue(this.Role.Id);
        // this.loadUIRoleMapping(role.Id);
    }

    clearRole(){
        this.RoleId?.setValue(null);
        this.Role?.setValue(null);
        this.emptyTable();
        this.updateView();
        this.UIRoleConfigForm.reset();
        this.form.resetForm();
        this.UIRoleConfigForm.markAsUntouched();
    }

    private _filterRoles(value: any) : any {
        const filterValue = (value ? (value.Code ? value.Code.toLowerCase() : value.toLowerCase()) : '');
        return this.roles.filter((o:any) => o.Code.toLowerCase().includes(filterValue));
    }

    displayWithRole(obj?: any): string | undefined {
        return obj ? obj.Code : undefined;
    }

    getMenuList(){
        this.uimenus = [];
        this.rest.getAll(this.global.getapiendpoint() + 'menu/GetAllActiveMenu/').subscribe((data : any) => {
            this.uimenus = data.Data;
        });
    }

    getRoleList(){
        this.roleList = [];
        this.rest.getAll(this.global.getapiendpoint() + 'role/GetAllActiveRole').subscribe(( data: any) => {
            this.roleList = data.Data;
            this.roles = data.Data;
        });
    }

    saveUIRoleConfig(){
        const UIRoleMap = this.UIRoleConfigForm.get('UIRoleMap')?.value;
        const model: any = { 
            RoleId: this.RoleId?.value,
            UIRoleMap: JSON.stringify(UIRoleMap),
            UserId: this.userLoggedIn.Id,
            UserRoleId: this.userLoggedIn.DefaultRoleId 
        }
        this.rest.create(this.global.getapiendpoint() + 'uirolemap/CreateUIRoleMap', model).subscribe((data : any) => {
            //this.toastr.showNotification('top', 'right', 'Mapping Successfully Added', 'success');
            this.emptyTable();
            this.updateView();
            this.UIRoleConfigForm.reset();
            this.form.resetForm();
            this.UIRoleConfigForm.markAsUntouched();
            //console.log("dataSource",this.dataSource)
        });
    }

    loadUIRoleMapping(roleId:any){
        this.isLoadingResults = true;
        this.emptyTable();
        this.updateView();
        this.rest.getById(this.global.getapiendpoint() + 'uirolemap/GetUIRoleMap/', roleId).subscribe((data: any) => {
            if(data.Success == true){
                if(data.Data.length !== 0) {
                    this.uiRoleMapStatus = 'update';
                    this.UIRoleMapData = data.Data;
                    this.uimenus.forEach((menu:any) => {
                        const UIRoleMap = this.UIRoleMapData.find(e => e.UIId === menu.Id);
                        if(UIRoleMap){
                            this.addRow({
                                UIId : menu.Id,
                                UI: menu.Title,
                                ParentId: menu.ParentId,
                                Viewer: UIRoleMap.Viewer,
                                Maker: UIRoleMap.Maker,
                                Checker: UIRoleMap.Checker,
                                Edit: UIRoleMap.Edit,
                                Export: UIRoleMap.Export,
                                Upload: UIRoleMap.Upload
                            }, false);
                        } else {
                            this.addRow ({
                                UIId: menu.Id,
                                UI: menu.Title,
                                ParentId: menu.ParentId,
                                Viewer: false,
                                Maker: false,
                                Checker: false,
                                Edit: false,
                                Export: false,
                                Upload: false,
                            }, false);
                        }
                    })
                    this.updateView();
                    this.isLoadingResults = false;
                }
                else{
                    this.setDefaultData();
                    this.updateView();
                    this.uiRoleMapStatus = 'create';
                }
            }else {
                console.error(data);
            }
        });
    }

    setDefaultData(){
        const menu_list = this.uimenus;
        menu_list.forEach((d:any) => {
            this.addRow({
                UIId: d.Id,
                UI: d.Title,
                ParentId: d.ParentId,
                Viewer: false,
                Maker: false,
                Checker: false,
                Edit: false,
                Export: false,
                Upload : false
            }, false)
        });
        this.updateView();
    }

    @HostListener('window:keyup', ['$event'])keyEvent(event: KeyboardEvent){
        if(event.altKey && event.keyCode == KEY_CODE.S_KEY){
            this.saveUIRoleConfig();
        }
    }

}