import { AfterViewInit, Component, ElementRef, OnInit, ViewChild, HostListener } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { RestService } from '../../services/rest.service';
import { Global } from '../../common/global';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
//import { MatPaginatorModule, MatTableDataSource, MatSortModule, MatTableModule } from '@angular/material';
// import { MatPaginatorModule} from '@angular/material/paginator';
// import {  MatSortModule } from '@angular/material/sort';
// import {  MatTableModule } from '@angular/material/table';
// import {MatAutocompleteModule} from '@angular/material/autocomplete';
// import {MatTableDataSource} from "@angular/material/table";
import { Location } from '@angular/common';
import { ExcelService } from '../../common/excel.service';
import { KEY_CODE } from '../../common/constant';
import { FlexyColumnComponent } from '../../common/flexy-column.component';

import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';

@Component({
    selector: 'app-role',
    templateUrl: './role.component.html',
    styleUrls: ['./role.component.scss']
})
export class RoleComponent implements OnInit, AfterViewInit {

    //@ViewChild(MatTableModule, { static: true, read: ElementRef }) private matTableRef!: ElementRef;
    RoleForm!: FormGroup;
    RoleList: boolean = false;
    RoleCreate: boolean = false;
    @ViewChild('topdiv', { static: true, read: ElementRef } ) topdiv!: ElementRef; 
    userLoggedIn: any;
    location: Location;
    UIObj: any;
    IsMaker: boolean = false;
    IsExport: boolean = false;
    IsEdit: boolean = false;

    //@ViewChild(MatPaginatorModule, { static: true }) paginator!: MatPaginatorModule;
    //@ViewChild(MatSortModule, { static: true }) sort!: MatSortModule;
    @ViewChild('form', { static: true }) form:any;

    @ViewChild(FlexyColumnComponent, { static: true }) flexyColumn!: FlexyColumnComponent;

    displayedColumns: string[] = ['Id', 'Code', 'Desc', 'IsCentralAccess','IsActive'];
    dataSource:any;
    @ViewChild(MatPaginator) paginator!: MatPaginator;
    @ViewChild(MatSort) sort!: MatSort;

    //dataSource:any;

    //displayedColumns: string[] = [];
    // columns: any[] = [
    //     { field: 'Id', width: 5 }, { field: 'Code', width: 30 }, { field: 'Desc', width: 60 } ,
    //      { field: 'IsCentralAccess', width: 5 }, { field: 'IsActive', width: 5 }
    // ];

    constructor(location: Location, private formBuilder: FormBuilder, private rest: RestService, private route: ActivatedRoute, private router: Router,
        private global: Global, private excelService: ExcelService) {
        this.location = location;
    }

    ngOnInit() {

        //this.displayedColumns = this.flexyColumn.setDisplayedColumns(this.columns);

        var path = this.location.prepareExternalUrl(this.location.path());
        if (path.charAt(0) === '#') {
            path = path.slice(2);
        }
        this.UIObj = this.global.getUIObj(path);
        this.IsMaker = this.UIObj.UIRoles[0].Maker;
        this.IsExport = this.UIObj.UIRoles[0].Export;
        this.IsEdit = this.UIObj.UIRoles[0].Edit;

        this.RoleForm = this.formBuilder.group({
            Id: [''],
            Code: ['', [Validators.required, Validators.pattern('[a-zA-Z0-9\\s\\(\\)\\-&.\\\\,/_]*'), Validators.maxLength(100)]],
            Desc: ['', [Validators.required, Validators.pattern('[a-zA-Z0-9\\s\\(\\)\\-&.\\\\,/_]*'), Validators.maxLength(2000)]],
            IsActive: [''],
            IsCentralAccess: ['']
        });

        this.getAllRoles();
        this.userLoggedIn = JSON.parse(localStorage.getItem('userLoggedIn')!);
    }

    get Id() { return this.RoleForm.get('Id'); }
    get Code() { return this.RoleForm.get('Code'); }
    get Desc() { return this.RoleForm.get('Desc'); }
    get IsActive() { return this.RoleForm.get('IsActive'); }
    get IsCentralAccess() { return this.RoleForm.get('IsCentralAccess'); } 

    getAllRoles() {
        this.rest.getAll(this.global.getapiendpoint() + 'role/GetAllRole').subscribe((data: any) => {

            let tableData:any = [];
            data.Data.forEach((element:any) => {
                tableData.push({ Id: element.Id, Code: element.Code, Desc: element.Desc, 
                IsCentralAccess: element.IsCentralAccess ? "Yes" : "No",
                IsActive: element.IsActive ? "Active" : "Inactive" });
            });

            this.dataSource = new MatTableDataSource(tableData);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
        });
        this.RoleCreate = false;
        this.RoleList = true;
    }

    // applyFilter(filterValue: string) {
    //     this.dataSource.filter = filterValue.trim().toLowerCase();
    // }

    applyFilter(event: Event) {
        const filterValue = (event.target as HTMLInputElement).value;
        this.dataSource.filter = filterValue.trim().toLowerCase();
    
        if (this.dataSource.paginator) {
          this.dataSource.paginator.firstPage();
        }
      }

    createRole() {
        //this.form.resetForm();
        this.RoleForm.reset();
        this.RoleForm.markAsUntouched();
        this.Code?.enable();
        this.Id?.setValue('');
        this.IsActive?.setValue(true);
        this.RoleCreate = true;
        this.RoleList = false;
    }

    backRole() {
        this.RoleCreate = false;
        this.RoleList = true;
        this.topdiv.nativeElement.scrollIntoView({ behavior: 'smooth' });
    }

    updateRole(Id: string) {

        this.rest.getById(this.global.getapiendpoint() + 'role/GetRoleById/', Id).subscribe((data: any) => {

            this.Id?.setValue(data.Data.Id);
            this.Code?.setValue(data.Data.Code);
            this.Code?.disable();
            this.Desc?.setValue(data.Data.Desc);
            this.IsActive?.setValue(data.Data.IsActive);
            this.IsCentralAccess?.setValue(data.Data.IsCentralAccess);
            this.RoleCreate = true;
            this.RoleList = false;
        });
    }

    exportRole() {
        this.excelService.exportASExcelFile(this.dataSource.filteredData, 'Role');
        //this.toastr.showNotification('top', 'right', 'Exported Successfully', 'success');
    }

    ngAfterViewInit() {
        //this.flexyColumn.setTableResize(this.matTableRef.nativeElement.clientWidth, this.columns);
    }

    onResizeColumn(event: any, index: number) {
        //this.flexyColumn.onResizeColumn(event, index, this.matTableRef);
    }

    @HostListener('window:resize', ['$event'])
    onResize(event:any) {
        //this.flexyColumn.setTableResize(this.matTableRef.nativeElement.clientWidth, this.columns);
    }

    @HostListener('window:keyup', ['$event']) keyEvent(event: KeyboardEvent) {

        if (event.altKey && event.keyCode == KEY_CODE.A_KEY) {
            this.createRole();
        }
        if (event.altKey && event.keyCode == KEY_CODE.B_KEY) {
            this.backRole();
        }
        if (event.altKey && event.keyCode == KEY_CODE.U_KEY) {
            // this.uploadRole();
        }
        if (event.altKey && event.keyCode == KEY_CODE.S_KEY) {
            this.saveRole();
        }
        if (event.altKey && event.keyCode == KEY_CODE.X_KEY) {
            this.exportRole();
        }
    }

    saveRole() {
        this.rest.checkDuplicate(this.global.getapiendpoint() + 'role/CheckDuplicateRole/', this.Code?.value.toString().trim(),
            (this.Id?.value !== '' ? this.Id?.value : '0')).subscribe((data: any) => {
                if (data.Data) {
                    //this.toastr.showNotification('top', 'right', data.Message, 'danger');
                }
                else {
                    var model: any = {
                        Id: this.Id?.value,
                        Code: this.Code?.value.trim(),
                        Desc: this.Desc?.value.trim(),
                        IsActive: this.IsActive?.value,
                        IsCentralAccess: this.IsCentralAccess?.value,
                        UserId: this.userLoggedIn.Id,
                        UserRoleId: this.userLoggedIn.DefaultRoleId
                    };
                    var apiUrl = '';
                    if (this.Id?.value == '') {
                        apiUrl = 'role/CreateRole';
                    }
                    else {
                        apiUrl = 'role/UpdateRole';
                    }
                    this.rest.create(this.global.getapiendpoint() + apiUrl, model).subscribe((data: any) => {
                        if (data.Success) {
                            //this.toastr.showNotification('top', 'right', data.Message, 'success');
                        }
                        else {
                           // this.toastr.showNotification('top', 'right', data.Message, 'danger');
                        }
                        this.getAllRoles();
                    });
                } 
            });
    } 
}