    IsMaker: boolean = false;
    IsExport: boolean = false;
    IsEdit: boolean = false;
    IsCentralAccess: boolean = false;
    Readonly = true;
    separatorKeysCodes: number[] = [ENTER, COMMA];
    roles: any = [];
    allRoles: any[] = [];
    allRolesInit: any[] = [];
    filteredRoles!: Observable<any[]>;
    @ViewChild('roleInput', { static: true }) roleInput!: ElementRef<HTMLInputElement>;
    @ViewChild('autoRole', { static: true }) autoRole!: any;
    @ViewChild('roleList', { static: true }) roleList:any;
    @ViewChild('form', { static: true }) form:any;
    displayedColumns: string[] = ['Id', 'LoginId', 'EmpCode', 'EmpName','RoleCode','IsActive'];
    dataSource:any;
    @ViewChild(MatPaginator) paginator!: MatPaginator;
    @ViewChild(MatSort) sort!: MatSort;
    @ViewChild(FlexyColumnComponent, { static: true }) flexyColumn!: FlexyColumnComponent;
    constructor(location: Location, private formBuilder: FormBuilder, private rest: RestService, private route: ActivatedRoute, private router: Router,
        private global: Global, private excelService: ExcelService) {
        this.location = location;
    }

    ngOnInit() {
        this.Readonly= false;
        var path = this.location.prepareExternalUrl(this.location.path());
        if (path.charAt(0) === '#') {
            path = path.slice(2);
        }
        this.UIObj = this.global.getUIObj(path);
        this.IsMaker = this.UIObj.UIRoles[0].Maker;
        this.IsExport = this.UIObj.UIRoles[0].Export;
        this.IsEdit = this.UIObj.UIRoles[0].Edit;

        this.UserForm = this.formBuilder.group({
            Id: [''],       
            ADUser: [''],
            SearchName: [''],
            LoginId: ['', [Validators.required, Validators.pattern('[a-zA-Z0-9_]*'), Validators.maxLength(100)]],
            EmpCode: [''],
            EmpName: ['', [Validators.required, Validators.pattern('[a-zA-Z0-9\\s\\(\\)\\-&.\\\\,/_]*'), Validators.maxLength(100)]],
            IsActive: [''],
            EmailId: ['', [Validators.required, Validators.email, Validators.maxLength(100)]],
            Activity: [''],
            Users: [''],
            Role: ['']            
        });

        this.getAllUsers();
        this.userLoggedIn = JSON.parse(localStorage.getItem('userLoggedIn')!);
      
        this.getAllRole();

        this.SearchName?.valueChanges.pipe(
            debounceTime(500)
        ).subscribe(text => {
            if (text != null && text != '') {
                this.users = [];
                this.getADUsers(text);
            }
            else {
                this.users = [];
            }
        });

        if(this.Role){
            this.filteredRoles = this.Role.valueChanges
            .pipe(startWith(null),
                map((value: any | null) => value ? this._filter(value, this.allRoles) : this.allRoles.slice())
            );
        }
        
    }

    get Id() { return this.UserForm.get('Id'); }
    //get RoleId() { return this.UserForm.get('RoleId'); }
    get Role() { return this.UserForm.get('Role'); }
    get IsActive() { return this.UserForm.get('IsActive'); }
    get SearchName() { return this.UserForm.get('SearchName'); }
    get LoginId() { return this.UserForm.get('LoginId'); }
    get EmpCode() { return this.UserForm.get('EmpCode'); }
    get EmpName() { return this.UserForm.get('EmpName'); }
    get EmailId() { return this.UserForm.get('EmailId'); }
    get Users() { return this.UserForm.get('Users'); }
    get ADUser() { return this.UserForm.get('ADUser'); }

    getAllUsers() {
        this.rest.getAll(this.global.getapiendpoint() + 'user/GetAllUser').subscribe((data: any) => {

            let tableData:any = [];
            data.Data.forEach((element:any) => {

                var rolecode = '';
                element.UserRoles.forEach((roleelement:any) => { rolecode += ", " + roleelement.Role.Code; });
                rolecode = rolecode.substr(2, rolecode.length);

                var entity = element.Entity ? element.Entity.Code : '';

                tableData.push({
                    Id: element.Id, LoginId: element.LoginId, EmpCode: element.EmpCode,
                    EmpName: element.EmpName, EmailId: element.EmailId,
                    RoleCode: rolecode,
                    IsActive: element.IsActive ? "Active" : "Inactive"
                });
            });

            this.dataSource = new MatTableDataSource(tableData);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
        });
        this.UserCreate = false;
        this.UserIndex = true;
    }

    applyFilter(event: Event) {
        const filterValue = (event.target as HTMLInputElement).value;
        this.dataSource.filter = filterValue.trim().toLowerCase();
    
        if (this.dataSource.paginator) {
          this.dataSource.paginator.firstPage();
        }
      }

    private _filter(value: any, obj: any): any {
        const filterValue = (value ? (value.Code ? value.Code.toLowerCase() : value.toLowerCase()) : "");
        return obj.filter((o:any) => o.Code.toLowerCase().includes(filterValue));
    }

