import { Component, ElementRef, OnInit, ViewChild, DoCheck } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Global } from '../common/global';
import { AuthService } from '../common/auth.service';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
// import { Toastr } from '../common/toastr';
import { RestService } from '../services/rest.service';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
// import { get } from 'https';
import { IpServiceService } from './IP-service.service';

@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
    @ViewChild("name", { static: true }) nameField!: ElementRef;

    Login_NewUser!: FormGroup;
    Login_ExistingUser!: FormGroup;
    Login_ExistingPartner!: FormGroup;

    hide = true;
    NewUser: boolean = false;
    ExistingUser: boolean = false;
    ExistingPartner: boolean = false;
    DvPOtp: boolean = false;

    VerifyMobile: boolean = false;
    VerifyEmail: boolean = false;
    EmailBtnText: string = "Verify Email";
    MobileBtnText: string = "Verify Mobile";
    //public cData:any;

    isverifyMobile: boolean = false;
    submitted = false;
    ipAddress: any;

    constructor(private formBuilder: FormBuilder, private route: ActivatedRoute, private router: Router,
        private global: Global, private authService: AuthService,
        public dialog: MatDialog, private rest: RestService, private httpClient: HttpClient,
        private ip: IpServiceService) { }

    ngOnInit() {
        // debugger;
        this.nameField.nativeElement.focus();
        this.Login_ExistingUser = this.formBuilder.group({
            UserName: ['', [Validators.required, Validators.pattern('[a-zA-Z0-9_]*'), Validators.maxLength(100)]],
            Password: ['', [Validators.required, Validators.maxLength(100)]]
        });

        this.Login_NewUser = this.formBuilder.group({
            fullName: ['', [Validators.required, Validators.pattern('^[a-zA-Z \-\']+'), Validators.maxLength(100)]],
            pancard: ['', [Validators.required, Validators.pattern('^[A-Z]{5}[0-9]{4}[A-Z]{1}'), Validators.maxLength(10)]],
            email: ['', [Validators.required, Validators.email]],
            MobileNo: ['', [Validators.required, Validators.pattern('^[0-9]+'), Validators.maxLength(10)]],  // ^[789]\d{9}$
            Id: ['']
        });

        this.Login_ExistingPartner = this.formBuilder.group({
            P_Mobile: ['', [Validators.required, Validators.pattern('^[0-9]+'), Validators.maxLength(10)]],
            P_OTP: ['', [Validators.required, Validators.maxLength(4)]]
        });

        this.authService.logout();
        this.Login_ExistingUser.markAsUntouched();

        // localStorage.setItem('PartnerID', JSON.stringify(""));        
        // localStorage.setItem('PartnerRegistrationOTP', JSON.stringify(""));
        // localStorage.setItem('PANNO', JSON.stringify(""));        
        this.ExistingUser = true;
    }


    IsExistingUser() {
        this.ExistingUser = true;
        this.NewUser = false;
        this.ExistingPartner = false;
    }

    IsNewUser() {
        this.NewUser = true;
        this.ExistingUser = false;
        this.ExistingPartner = false;
    }

    get UserName() { return this.Login_ExistingUser.get('UserName'); }
    get Password() { return this.Login_ExistingUser.get('Password'); }

    get Id() { return this.Login_NewUser.get('Id'); }
    get fullName() { return this.Login_NewUser.get('fullName'); }
    get pancard() { return this.Login_NewUser.get('pancard'); }
    get email() { return this.Login_NewUser.get('email'); }
    get MobileNo() { return this.Login_NewUser.get('MobileNo'); }

    get P_Mobile() { return this.Login_ExistingPartner.get('P_Mobile'); }
    get P_OTP() { return this.Login_ExistingPartner.get('P_OTP'); }

    loginUser() {
        var model = {
            UserName: this.UserName?.value,
            Password: this.Password?.value,
            fullName: this.fullName?.value,
            pancard: this.pancard?.value,
            email: this.email?.value,
            MobileNo: this.MobileNo?.value,
            Id: this.Id?.value
        };

        this.rest.create(this.global.getapiendpoint() + 'Login/AuthenticateUser', model).subscribe((data: any) => {
            if (data.Success) {
                // console.log(data);
                this.rest.getById(this.global.getapiendpoint() + 'menu/GetAllMenuById/', data.Data.DefaultRoleId).subscribe((menudata: any) => {
                    localStorage.setItem('isLoggedIn', "true");
                    localStorage.setItem('userLoggedIn', JSON.stringify(data.Data));
                    localStorage.setItem('menuItems', JSON.stringify(menudata.Data));
                    this.router.navigate(['/ltc/asset']);
                });
            }
            else {
                // this.toastr?.showNotification('top', 'right', data.Message, 'danger');
            }
        });
    }

    Clear() {
        this.Login_NewUser.reset();
        this.Login_NewUser.markAsUntouched();
        this.fullName?.setValue('');
        this.pancard?.setValue('');
        this.MobileNo?.setValue('');
        this.email?.setValue('');
    }

    CheckAPIFromNode() {
        debugger;

        this.ip.getIPAddress().subscribe((res: any) => {
            this.ipAddress = res.ip;
            //console.log(this.ipAddress);
        });

        //console.log(this.ipAddress);

        // var ObjModel: any = {
        //     path: '/NewTech/Bajaj/SendSMS',
        //     data: JSON.stringify({
        //         Mobile: "7350251534",
        //         Body: "Test"
        //     })
        // };
        // var apiUrl = 'Login/SavePartnerRegister';

        // this.rest.create(this.global.getapiendpoint() + 'Login/PortalAPI', ObjModel).subscribe((data: any) => {
        //     if (data.Data) {
        //         this.toastr.showNotification('top', 'right', data.Message, 'danger');
        //     }
        //     else {
        //         this.toastr.showNotification('top', 'right', data.Message, 'success');
        //     }
        // });
    }

    SubmitPartnerLoginOtp() {
        debugger;
        var StoaredPLOtp = JSON.parse(localStorage.getItem('PLMobileOTP')!);
        if (StoaredPLOtp != this.P_OTP?.value) {
            // this.toastr.showNotification('top', 'right', "Please enter correct OTP", 'danger');
        }
        else {
            var StoredPan = JSON.parse(localStorage.getItem('PANNO')!);

            this.rest.getById(this.global.getapiendpoint() + 'Login/AuthenticatePartner/', StoredPan).subscribe((data: any) => {
                if (data.Success) {
                    this.rest.getById(this.global.getapiendpoint() + 'menu/GetAllMenuById/', "1").subscribe((menudata: any) => {
                        localStorage.setItem('isLoggedIn', "true");
                        localStorage.setItem('userLoggedIn', JSON.stringify(data.Data));
                        localStorage.setItem('menuItems', JSON.stringify(menudata.Data));

                        localStorage.removeItem('PLMobileOTP');
                        localStorage.setItem('PLMobileOTP', "");

                        this.router.navigate(['/ltc/asset']);
                    });
                }
                else {
                    // this.toastr.showNotification('top', 'right', data.Message, 'danger');
                }
            });
        }
    }
}